import React from 'react';
import Latex from 'react-latex';
export default function AutovaloresAutovetores(){ return(<>

<h2>
    Autovalores e Autovetores
</h2>

Considere uma matriz <Latex>{"$A^{nxn}$"}</Latex>, e a transformação linear <Latex>{"$T(\\vec{x}) = A\\vec{x}$"}</Latex>, para um vetor <Latex>{"$\\vec{x}\\mathrel{\\char`≠}\\vec{0},\\vec{x}\\in R^n$"}</Latex>. Então, se

<Latex displayMode={true}>{'$$'+
'A\\vec{x}=\\lambda \\vec{x}\\Leftrightarrow (A - \\lambda\\mathbf{I})\\vec{x}=\\mathbf{0}'
+'$$'}</Latex>

então <Latex>{"$\\lambda$"}</Latex> e um autovalor e <Latex>{"$\\vec{x}$"}</Latex> é um autovetor. O sistema linear <Latex>{"$(A - \\lambda\\mathbf{I})\\vec{x}=\\mathbf{0}$"}</Latex> tem uma solução <Latex>{"$\\vec{x}\\mathrel{\\char`≠}\\vec{0}$"}</Latex>, se e somente se, <Latex>{"$|(A - \\lambda\\mathbf{I})\\vec{x}|=0$"}</Latex>

<br />
<h3>Calculando Autovalores</h3>
Para uma matriz <Latex>{"$A^{2x2}$"}</Latex> temos que os auto valores são as raizes do seguinte polinômio

<Latex displayMode={true}>{'$$'+
'|(A - \\lambda\\mathbf{I})\\vec{x}| = |A_{(1,2)}| -\\lambda a_{11}-\\lambda a_{22} + \\lambda^2'
+'$$'}</Latex>

Onde <Latex>{"$A_{(1,2)}$"}</Latex>, é a matriz A mantendo apenas as linhas e colunas 1 e 2.

<br />

Para uma matriz <Latex>{"$A^{3x3}$"}</Latex> temos que os auto valores são as raizes do seguinte polinômio

<Latex displayMode={true}>{'$$'+
'|(A - \\lambda\\mathbf{I})\\vec{x}| = |A_{(1,2,3)}| - \\lambda(|A_{(1,2)}|+|A_{(1,3)}|+|A_{(2,3)}|) + \\lambda(a_{11} + a_{22} + a_{33}) - \\lambda^3'
+'$$'}</Latex>

Para uma matriz <Latex>{"$A^{nxn}$"}</Latex> temos que os auto valores são as raizes do seguinte polinômio

<Latex displayMode={true}>{'$$'+
'\\text{em processo de validação}'
+'$$'}</Latex>


<b>Teorema :</b> Suponha que A é uma matriz real e simétrica (<Latex>{"$A = A^T$"}</Latex>). Então <Latex>{"$\\lambda_1, \\dots, \\lambda_n$"}</Latex> são reais.

<br />
<b>Prova : </b> Para <Latex>{"$\\vec{x}\\mathrel{\\char`≠}\\vec{0},\\vec{X}\\in\\mathbb{R}^n$"}</Latex>, temos <Latex>{"$A\\vec{x}=\\vec{b}$"}</Latex>, então

<Latex displayMode={true}>{'$$'+
'{\\vec{b}}^{T}\\vec{b}='+
'{(A\\vec{x})}^{T}A\\vec{x}='+
'{\\vec{x}}^{T}{A}^{T}A\\vec{x}='+
'{\\vec{x}}^{T}AA\\vec{x}='+
'\\lambda{\\vec{x}}^{T}A\\vec{x}='+
'\\lambda^2{\\vec{x}}^{T}\\vec{x}='+
'\\lambda^2{||\\vec{x}||}^{2} \\Leftrightarrow'+
'{\\vec{b}}^{2} =\\frac{{||\\vec{b}||}^{2}}{{||\\vec{x}||}^{2}}'
+'$$'}</Latex>

Já que <Latex>{"${||\\vec{b}||}^{2},{||\\vec{x}||}^{2}\\geq0$"}</Latex>, então <Latex>{"$\\lambda\\in\\mathbb{R}$"}</Latex>




</>)}